<template>
  <div class="dashboard-container">
    <div class="categorys-container">
      <div class="category-container" @click="changeCategory('tasks')">
        TASKS
      </div>

      <div class="category-container" @click="changeCategory('materials')">
        MATERIALS
      </div>

      <div class="category-container" @click="changeCategory('personal')">
        PERSONAL
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "dashboard",
  methods: {
    changeCategory(management_change) {
      this.$router.push(`/management/${management_change}`)
    }
  },
  beforeMount() {
    this.$store.commit('SET_NAVBAR_TITLE', 'DASHBOARD')
    this.$store.commit('SET_PREVIUS_PAGE', '/projects')
    this.$store.commit('management/SET_CURRENT_TAB', 0)
  }
};
</script>

