<template>
  <nav class="navbar-container">
    <button class="back-button" @click="goBack">
      <i class="fa fa-arrow-left"></i>
    </button>
    <div class="spacerMobile"></div>
    <span class="title">
      {{ navbar_title }}
    </span>
    <div class="spacerMobile"></div>
    <dropdown :options="menu_options"></dropdown>
  </nav>
</template>

<script>
import dropdown from '@/components/dropdown/dropdown'

export default {
  name: "Navbar",
  components: {
    dropdown
  },
  computed: {
    navbar_title() {
      return this.$store.state.root.navbar_title
    }
  },
  methods: {
    goBack() {
      if(this.$store.state.root.previus_page == "/") {
        this.$auth.logout();
        this.$router.push(this.$store.state.root.previus_page)
      } else {
        this.$router.push(this.$store.state.root.previus_page)
      }
    }
  },
  data() {
        return {
        menu_options: {
            type: Object,
            default: null,
        }
        }
    },
  beforeMount() {
      this.menu_options = {
      salados: ["NEW PROJECT", "/projects"],
      dulces: ["SOMETHING", "/dashboard"]
      }
  }
};
</script>

<style src="./navbar.scss" lang="scss" scoped></style>