<template>
  <div class="login-container">
    <Card :shadow="false">
      <form
        @submit.prevent="handleSubmit"
        name="contacto"
        method="post"
        class="forms-container"
      >
        <div class="column-container">
          <img
            src="/images/cropped-Avonce_Logo_header-4.png"
            alt="Logo"
            class="logo-login"
          />
          <div class="row-container">
            <customInput
              v-model="loginData.username"
              v-bind="{
                identifier: 'username',
                label: 'Usuario',
                icon: 'user',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="loginData.password"
              v-bind="{
                identifier: 'password',
                label: 'Contraseña',
                icon: 'lock',
                type: 'password'
              }"
            />
          </div>
          <transition name="FadeIn">
            <div class="row-container" v-show="showError">
              <span class="error-message">
                Usuario y/o contraseña incorrectos.
              </span>
            </div>
          </transition>
        </div>
        <button class="boton-enviar">
          ENVIAR
        </button>
      </form>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/card/card";
import customInput from "@/components/custominput/custominput";
import { mapState } from "vuex";

export default {
  name: "login",
  components: {
    Card,
    customInput
  },
  data() {
    return {
      loginData: {
        username: "",
        password: ""
      }
    };
  },
  computed: {
    ...mapState("auth", {
      showError: "showErrorMessageLogin"
    })
  },
  methods: {
    handleSubmit() {
      this.$store.commit("auth/SET_SHOW_ERROR_MESSAGE_LOGIN", false);
      setTimeout(() => {
        let response = this.$auth.login(this.loginData);
        response.catch(e => {
          console.error(e);
          this.$store.commit("auth/SET_SHOW_ERROR_MESSAGE_LOGIN", true);
        });
      }, 200);
    }
  },
  watch: {
    '$route'() {
      if(this.$store.state.root.previus_page) {
        this.$auth.logout();
      }
    }
  }
};
</script>

