import Vue from "vue";
import Vuex from "vuex";
import root from "./modules/root.store"
import auth from "./modules/auth.store"
import management from "./modules/management.store"
import tasks from "./modules/tasks.store"
import materials from "./modules/materials.store"
import personal from "./modules/personal.store"
import selection from "./modules/selection.store"


Vue.use(Vuex);

const store = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  modules: {
    root,
    auth,
    management,
    tasks,
    materials,
    personal,
    selection
  }
});

store.watch((state) => state.selection.items_selected, (newValue) => {
  newValue.length > 0 ?
    store.commit('selection/SET_SELECTION_ACTIVE', true) :
    store.commit('selection/SET_SELECTION_ACTIVE', false);
})

export default store
